// JS IMPORTS
import React, {Component} from 'react';
import {/* Route, */ Link} from 'react-router-dom'

import {Template} from 'react-jsx-template'
import I from '../plugins/Icons' // Global SVG icon kit






export default class BottomNav extends Component {
  constructor(props) {
    super(props)

    this.navList = ''
    this.state = {
      nav: [
        {
          name: 'services',
          route: '/services',
          icon: () => <I.Services />,
          className: 'blue--text',
          childRoutes: [
            {
              name: 'software',
              route: '/software',
              icon: I.Pointer,
              className: 'lightblue'
            },

            {
              name: 'graphics',
              route: '/graphics',
              icon: I.Pointer,
              className: 'lightblue'
            },

            {
              name: 'marketing',
              route: '/marketing',
              icon: I.Pointer,
              className: 'lightblue'
            },

            {
              name: 'legal',
              route: '/legal',
              icon: I.Pointer,
              className: 'lightblue'
            }
          ]
        },

        {
          name: 'blog',
          route: '/blog',
          icon: () => <I.Blog />,
          className: 'blue--text',
          childRoutes: [
            {
              name: 'news',
              route: '/news',
              icon: I.Pointer,
              className: 'lightblue'
            },

            {
              name: 'tutorials',
              route: '/tutorials',
              icon: I.Pointer,
              className: 'lightblue'
            },

            {
              name: 'legal',
              route: '/legal',
              icon: I.Pointer,
              className: 'lightblue'
            }
          ]
        },

        {
          name: 'profile',
          route: '/profile',
          icon: () => <I.Profile />,
          className: 'blue--text',
          childRoutes: [
            {
              name: 'bio',
              route: '/bio',
              icon: I.Pointer,
              className: 'lightblue'
            },

            {
              name: 'portfolio',
              route: '/portfolio',
              icon: I.Pointer,
              className: 'lightblue'
            },
            
            {
              name: 'github',
              route: '/github',
              icon: I.Pointer,
              className: 'lightblue'
            }
          ]
        },

        {
          name: 'contact',
          route: '/contact',
          icon: () => <I.Contact />,
          className: 'blue--text',
          childRoutes: [
            {
              name: 'email',
              route: '/email',
              icon: I.Pointer,
              className: 'lightblue'
            },

            {
              name: 'phone',
              route: '/phone',
              icon: I.Pointer,
              className: 'lightblue'
            },
            
            {
              name: 'social media',
              route: '/social-media',
              icon: I.Pointer,
              className: 'lightblue'
            }
          ]
        }
      ]
    }
  }

  componentDidMount () {
    this.navList = this.state.nav.map((item, index) => {
      return (
        <li key={index}>
          <Link to={item.route} className={item.className}>
            <div>
              {item.icon()}
            </div>

            {item.name}
          </Link>
        </li>
      )
    })
  }

  render () {
    this.navList = this.state.nav.map((item, index) => {
      return (
        <li key={index}>
          <Link to={item.route} className={item.className}>
            <div className='icon-holder'>
              {item.icon()}
            </div>
            
            <span>{item.name}</span>
          </Link>
        </li>
      )
    })

    return (
      <Template>
        <nav id='bottom-nav'>
          <ul>
            {this.navList}
          </ul>
        </nav>

        <style>
        {`
            ul {
              display: flex;
              height: 4rem;
              width: 100vw;
              justify-content: space-between;
              padding: 3%;
              padding-top: 5px;
              padding-bottom: 0px;
            }

            li {
              display: block;
              min-width: 15%;
              height: 4rem;
            }

            .icon-holder {
              width: 1.25rem;
              height: 1.5rem;
              margin: auto;
            }

            /* SM */
              @media only screen and (min-width: 600px) {
                ul {
                  width: 70vw;
                  justify-content: space-around;
                  margin: auto;
                }
              }

              /* MD */
              @media only screen and (min-width: 960px) {
                
              }

              /* LG */
              @media only screen and (min-width: 1280px) {
                ul {
                  width: 50vw;
                  justify-content: space-around;
                  margin: auto;
                }
              }

              /* XL */
              @media only screen and (min-width: 1920px) {

              }
        `}
        </style>
      </Template>
    )
  }
} 
