// JS IMPORTS
import React, {Component} from 'react';
import MDC from '../plugins/MaterialUIComponents' // Material-UI component kit
//import I from '../plugins/Icons' // Global SVG icon kit
import {Template} from 'react-jsx-template' // Wrapper component providing encapsulation using shadow DOM API

// IMAGES





export default class Home extends Component {
  render() {
    return (
      <Template>
        <MDC.Box className='contact white'>
          <h1>Contact</h1>
        </MDC.Box>

        <style>
          {`
              :host {
                min-height: 100%;
                overflow: hidden;
                overflow-y: auto !important;
              }
          `}
        </style>
      </Template>
      
    )
  }
}
