// JS Imports
import React, {Component} from 'react';
// import {useHistory} from 'react-router-dom'

import Home from './Home'
import Services from './Services'
import Blog from './Blog'
import Profile from './Profile'
import Contact from './Contact'
import BottomNav from '../components/BottomNav'

import {Template} from 'react-jsx-template' // Wrapper component providing encapsulation using shadow DOM API
import MD from '../plugins/MaterialUIComponents' // Material-UI component kit
import FN from '../plugins/Functions' // abstracted reusable functions



export default class AppWindow extends Component {
  constructor(props){
    super(props)

    this._route = this.props.route
    this._setState = FN.setState.bind(this) // bind 'this' to 'setState' function
    this.state = {
      landing: true,
      showHomePage: true
    }
    this.panel = null
    this.rootRef = React.createRef()
    this.showHomePage = true
    this.scheduledUpdate = true
    this.windowHeight = window.innerHeight
    this.windowWidth = window.innerWidth
  }

  componentDidMount() {
    let shadow = this.rootRef.current

    shadow.ready((shadow) => {
      this.panel = shadow.querySelector('.app-window')
      this.panel.scrollTo({top: 0, left: this.windowWidth, behavior: 'smooth'})
      this.panel.addEventListener('scroll', () => {})

      // getting elements by ref in the shadow DOM
      // console.log(shadow.getElementByRef(this.testRef))
    })
  }

  componentDidUpdate() {
 
  }

  render() {
    return (
      <Template ref={this.rootRef}>
        <MD.Box className='app-window'>
          <ul className={this.props.route}>
            <li>
              <ul>
                <li>
                  <Services />
                </li>

                <li>
                  <Blog />
                </li>

                <li>
                  <Profile />
                </li>

                <li>
                  <Contact />
                </li>
              </ul>
            </li>

            {
              this.state.showHomePage
              ? <li>
                  <Home hideHomePage={this._setState('landing', false)} showNav={this.props.showNav} />
                </li>

              : null
            }

            <li id='footer-menu'>
              <BottomNav />
            </li>
          </ul>
        </MD.Box>

        <style>
          {`
              :host {
                z-index: 2;
                overflow-x: auto !important;
              }

              ul.${this.props.route} {position: relative;}

              // ul.home {width: 200vw !important;}

              ul {
                display: block;
                height: calc(100vh - 4rem);
                width: 200vw;
                padding: 0px;
                margin: 0px;
                overflow: hidden;
                overflow-x: auto !important;
              }

              ul ul {width: 400vw;}

              li {
                display: inline-block;
                height: 100%;
                width: 100vw;
                padding: 0px;
                margin: 0px;
              }

              li li {
                overflow: hidden;
                overflow-y: auto !important;
              }

              li:not(:last-of-type) {
                overflow-x: auto !important;
              }

              .home #footer-menu {
                height: 4rem;
                width: 100vw;
                position: relative;
                top: 0rem !important;
                float: left;
                box-shadow: -0.05px -0.05px 5px 0.02px var(--lightblue);
                background: var(--white);
                transition-duration: 2s;
              }

              .${this.props.route} #footer-menu {
                height: 4rem;
                width: 100vw;
                position: relative;
                top: -4rem;
                float: left;
                box-shadow: -0.05px -0.05px 5px 0.02px var(--lightblue);
                background: var(--white);
                transition-duration: 2s;

              }

              /* SM */
              @media only screen and (min-width: 600px) {
                
              }

              /* MD */
              @media only screen and (min-width: 960px) {

              }

              /* LG */
              @media only screen and (min-width: 1280px) {
                
              }

              /* XL */
              @media only screen and (min-width: 1920px) {

              }
          `}
        </style>
      </Template>
    )
  }
}
