// JS IMPORTS
import React, {Component} from 'react';
import MD from '../plugins/MaterialUIComponents' // Material-UI component kit
//import I from '../plugins/Icons' // Global SVG icon kit
import {Template} from 'react-jsx-template' // Wrapper component providing encapsulation using shadow DOM API


// IMAGES





export default class Services extends Component {
  render() {
    return (
      <Template>
        <MD.Box className='services white'>
          <h1>Services</h1>

          <p>Software development</p>
          <p>Graphic design</p>
          <p>Branding and Marketing</p>
        </MD.Box>

        <style>
          {`
              :host {
                min-height: 100%;
                overflow: hidden;
                overflow-y: auto !important;
              }
          `}
        </style>
      </Template>
    )
  }
}
